/*   footer.scss                 */
/*-------------------------------*/


footer {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: var(--Theme-Color-White);
    
    @media #{$brk-desktop} {
	width: 80%;
	margin: 0 auto;
	flex-direction: row;
	justify-content: center;
    }

    @media #{$brk-max} {
	width: 70%;
    }
    
}



.social-icons {
    list-style: none;
    padding: 0;
    text-decoration: none;
}

.social-icons ul {
    margin: 0 5% 0 5%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;


    @media #{$brk-desktop} {
	align-items: unset;
	justify-content: unset;
	margin: 0;
	margin-right: auto;
    }
}

.social-icons li {
    // margin: 0 5%;
    margin: 0 4%;

    @media #{$brk-tablet} {
	//margin: 0 25% 0 25%;
	margin: 0 20% 0 20%;
    }

    @media #{$brk-desktop} {
	//margin: 0 15% 0 15%;
	margin: 0 8% 0 8%;
    }

    & li:first-child {
	margin-left: 0;

	@media #{$brk-desktop} {
	    margin-left: 0;
	}
    }

    & li:last-child {
    	margin-right: 0;

	@media #{$brk-desktop} {
	    margin-right: 0;
	}
    }
}

