.social-icons a {
    display: block;
    margin: 0;
}




.social-icons svg {
    opacity: 1;
    fill: var(--Theme-Color-Grey);
    width: 50px;
    height: 50px;
    padding: 10px;

    &:hover, :focus {
	fill: var(--Theme-Color-Grey-Dk);
	transform: scale(1.1);
	transition: 0.3s ease-in-out;
    }
}

