.copyright {
    letter-spacing: 1px;
    text-transform: uppercase;
    margin-bottom: 20px;

    @media #{$brk-tablet} {
	margin-top: 5px;
	margin-bottom:30px;
    }
    
    @media #{$brk-desktop} {
	margin-top: 30px;
	margin-bottom: 30px;
	margin-left: auto;
    }
}

