/* _variables.scss (Global Settings)                                           */
/* =========================================================================== */
/*
   Google Fonts - https://fonts.google.com/
   Fonts: Cormorant Garamond (Regular 400 & Regular 400 Italic, Bold 700 & Bold 700 Italic)
          Nunito Sans (Regular 400 & Regular 400 Italic, Bold 700)
          Quattrocento Sans (Regular 400 & Regular 400 Italic, Bold 700)
*/
/* Any padding or border that’s applied
   will not be added to the rendered width */
*, *:before, *:after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* SCSS Variables */
/* Global CSS3 Variables */
:root {
  --font-family-intro: 'Cormorant Garamond', Georgia, serif;
  --font-family--heading:      'Quattrocento Sans', Geneva, Tahoma, sans-serif;
  --font-family--body:         'Nunito Sans', Verdana, sans-serif;
  --intro-weight-regular:       400;
  --intro-weight-italic:        400i;
  --intro-weight-bold:          700;
  --intro-weight-bold-italic:   700i;
  --heading-weight-regular:     400;
  --heading-weight-italic:      400i;
  --heading-weight-bold:        700;
  --body-weight-regular:        400;
  --body-weight-italic:         400i;
  --body-weight-bold:           700;
  --Theme-Color-Lt:             #00d6db;
  --Theme-Color:                #00b1db;
  --Theme-Color-Md:             #009fdb;
  --Theme-Color-Dk:             #007adb;
  --Theme-Color-Yellow:         #eccd10;
  --Theme-Color-Red:            #ec3516;
  --Theme-Color-Grey-Lt:        #ecebeb;
  --Theme-Color-Grey-Md:        #d9d7d7;
  --Theme-Color-Grey:           #969091;
  --Theme-Color-Grey-Dk:        #515151;
  --Theme-Color-Black:          #474747;
  --Theme-Color-White:          #fff;
  --Theme-Color-White-Lt:       #f5f5f5;
  --Body-Text-Color:            #666;
  --figCaption-Color:           rgba(0, 0, 0, 0.9);
  --Overlay:                    rgba(0, 0, 0, 0.5);
  --Theme-Color-Orange:         #e77a3b;
  --min-width: 320px;
  --max-width: 1440px;
}

html, body {
  min-height: 100%;
  min-width: var(--min-width);
  max-width: var(--max-width);
  margin: 0 auto;
  background: var(--Theme-Color-Grey-Md);
}

/* ========================================================================== */
/*   _base.scss  (body, h1, p etc)                                            */
/* ========================================================================== */
body {
  color: var(--Body-Text-Color);
  font-size: 16px;
  line-height: 1.2em;
  font-family: var(--font-family--body);
  font-weight: var(--body-weight-regular);
  background: #fff;
  color: var(--Body-Text-Color);
  height: 100vh;
}

h1, h2, h3 {
  font-family: var(--font-family--heading);
}

h1 {
  font-weight: var(--heading-weight-bold);
  font-size: 2em;
  letter-spacing: 0.2em;
}

h2 {
  font-size: 1.5em;
}

@media (min-width: 768px) {
  h3 {
    padding-bottom: .25em;
  }
}

figure {
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
}

