    font-size: ;
    font-style: ;
    line-height: ;
    font-weight: ;
    margin: ;
    padding: ;
    border-left: solid size color, ;
}
*/
/* Lists */
ul,
li {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

/* Form Elements */
input,
textarea {
  display: block;
  outline: 0;
  border: 0;
}

/*
input:focus, 
textarea:focus {
	transition: ;
}
*/
/* Button */
button {
  outline: 0;
}

/* ========================================================================== */
/* Layout Styles - Define layout style for major sections of a page           */
/*                 Grid, Sections, Header, footer, sidebar                    */
/* ========================================================================== */
/* layout Folder Imports */
/*----------------------------------*/
/* _containers.scss                 */
/*----------------------------------*/
body#container {
  scroll-behavior: smooth;
  scroll-snap-type: y mandatory;
}

section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 8vh 4vw;
}

section#intro {
  padding-top: 6em;
}

section#webdev {
  background-color: var(--Theme-Color-Dk);
}

section#graphic {
  background-color: var(--Theme-Color-Black);
}

section#colophon,
section#contact,
section#intro {
  background-color: var(--Theme-Color-White);
}

.introduction {
  font-family: var(--font-family-intro);
  font-weight: var(--heading-weight-bold);
  font-style: italic;
  font-size: 3em;
}

.section_head {
  font-family: var(--font-family--heading);
  font-weight: var(--heading-weight-bold);
  padding: 1em 0;
  color: var(--Theme-Color-White);
  text-align: right;
}

/*----------------------------------*/
/* _gallery.scss                 */
/*----------------------------------*/
.gallery {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(3, auto);
  grid-gap: 5px;
}

/* ========================================================================== */
/* _header.scss                                                               */
/* ========================================================================== */
header {
  position: fixed;
  top: 0;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  height: 55px;
  background: var(--Theme-Color);
  color: var(--Theme-Color-White);
  padding: 10px 0;
  z-index: 1;
}

@media (min-width: 768px) {
  header {
    height: 56px;
    justify-content: space-between;
  }
}

.logo {
  margin: 0;
  text-transform: uppercase;
  font-size: 20px;
}

.logo a {
  color: var(--Theme-Color-White-Lt);
  text-decoration: none;
  display: block;
  padding: 16px;
}

@media (min-width: 768px) {
  .logo a {
    padding: 17px 25px;
  }
}

/* ========================================================================== */
/* _navbar.scss                                                               */
/* ========================================================================== */
.main-nav {
  position: absolute;
  top: 100%;
  width: 100%;
  background: var(--Theme-Color-Grey);
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.2s ease-in-out;
}

@media (min-width: 768px) {
  .main-nav {
    position: relative;
    right: unset;
    max-height: unset;
    background: transparent;
  }
}

.main-nav ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

@media (min-width: 768px) {
  .main-nav ul {
    display: flex;
    flex-direction: row;
  }
}

.main-nav li {
  border-bottom: 1px solid var(--Theme-Color-Grey-Md);
  letter-spacing: 0.0625em;
}

@media (min-width: 768px) {
  .main-nav li {
    border-bottom: none;
  }
}

.main-nav:last-child {
  border-bottom: none;
}

.main-nav a {
  color: var(--Theme-Color-White-Lt);
  text-decoration: none;
  display: block;
  padding: 2em 7em;
  text-transform: uppercase;
  text-align: center;
}

@media (min-width: 768px) {
  .main-nav a {
    margin: 0;
    padding: 17px;
  }
}

.logo a:hover,
.logo a:focus,
.main-nav a:hover,
.main-nav a:focus {
  background: var(--Theme-Color-Yellow);
  color: var(--Body-Text-Color);
}

/* Mobile Nav Settings ----------------- */
.mobile-nav {
  padding: 1em 2vw 1em 1em;
  position: absolute;
  right: .5em;
  cursor: pointer;
}

@media (min-width: 768px) {
  .mobile-nav {
    display: none;
  }
}

.mobile-nav--open {
  transform: rotate(45deg);
}

.mobile-nav--open .hamburger::before {
  opacity: 0;
}

.mobile-nav--open .hamburger::after {
  transform: translateY(-3px) rotate(-90deg);
}

.hamburger,
.hamburger::before,
.hamburger::after {
  content: '';
  display: block;
  background: var(--Theme-Color-White-Lt);
  height: 3px;
  width: 1.75em;
  border-radius: 3px;
  transition: all 0.3s ease-in-out;
}

.hamburger::before {
  transform: translateY(-7px);
}

.hamburger::after {
  transform: translateY(4px);
}

/* ========================================================================== */
/* _about.scss                                                                */
/* ========================================================================== */
.bio-avatar {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: auto;
}

.bio-avatar p {
  grid-column: 1/3;
  margin-bottom: 1em;
}

.bio-avatar img {
  grid-column: 1/3;
  width: 38%;
  border: 2px solid var(--Theme-Color-White);
  border-radius: 50%;
  justify-self: center;
  margin-bottom: 1em;
}

.bio-skills h3 {
  margin-bottom: .5em;
}

.bio-skills ul li {
  padding-left: 0.25em;
  list-style: square inside;
  color: var(--Theme-Color);
}

