    border-radius: 50%;
    justify-self: center;
    margin-bottom: 1em;
}
*/
/* ========================================================================== */
/* _modal.scss                                                                */
/* ========================================================================== */
.overlay {
  display: none;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  overflow: auto;
  background-color: var(--Overlay);
  animation-name: overlayopen;
  animation-duration: 0.5s;
}

.modal {
  display: none;
  background-color: var(--Theme-Color-White);
  border: 2px solid var(--Theme-Color-Black);
  color: var(--Theme-Color-Black);
  width: 90vw;
  height: 90vh;
  margin: 0 auto;
  overflow-y: scroll;
  box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0.2), 0 7px 20px 0 rgba(0, 0, 0, 0.17);
}

@media (min-width: 768px) {
  .modal {
    border: 3px solid var(--Theme-Color-Black);
  }
}

.modal img {
  width: 100%;
  overflow: hidden;
  border-bottom: 1px solid var(--Theme-Color-Black);
}

.modal h3 {
  color: var(--Theme-Color);
  padding-bottom: 0.5em;
  font-size: 1.25em;
}

@media (min-width: 768px) {
  .modal h3 {
    font-size: 1.5em;
  }
}

.modal h4 {
  color: var(--Theme-Color-Grey);
}

.modal ul {
  font-size: 0.9em;
  padding-left: 0.25em;
  padding-bottom: 1em;
  list-style: square inside;
  color: var(--Theme-Color);
}

.modal p {
  font-size: 0.9em;
  padding: 0 .25em 0 .25em;
}

@media (min-width: 768px) {
  .modal p {
    padding: 0 1.2em 0 1.2em;
  }
}

@media (min-width: 1024px) {
  .modal p {
    padding: 0 1.25em 0 1.25em;
  }
}

.modal p::first-line {
  font-weight: var(--body-weight-bold);
}

/*
.modal p:first-line {
    font-weight: var(--body-weight-bold);
}
*/
.modalSite {
  margin: 2em 0 1.5em 0;
}

.modalSite a {
  text-decoration: none;
  padding: 1em 1em 1em 1em;
  background: var(--Theme-Color);
  color: var(--Theme-Color-White);
}

.modalSite a:hover {
  background: var(--Theme-Color-Yellow);
  color: var(--Theme-Color-Black);
}

/*

