    text-decoration: none;
    padding: 1em 1em 1em 1em;
    background: var(--Theme-Color);
    color: var(--Theme-Color-White);
}

.modalSite a:hover {
    background: var(--Theme-Color-Yellow);
    color: var(--Theme-Color-Black);
}
*/
.galleryItem-info {
  padding: 10px;
}

@media (min-width: 768px) {
  .galleryItem-info {
    padding: 20px;
  }
}

@keyframes overlayopen {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/*-------------------------------*/
/*   footer.scss                 */
/*-------------------------------*/
footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: var(--Theme-Color-White);
}

@media (min-width: 1024px) {
  footer {
    width: 80%;
    margin: 0 auto;
    flex-direction: row;
    justify-content: center;
  }
}

@media (min-width: 1440px) {
  footer {
    width: 70%;
  }
}

.social-icons {
  list-style: none;
  padding: 0;
  text-decoration: none;
}

.social-icons ul {
  margin: 0 5% 0 5%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

@media (min-width: 1024px) {
  .social-icons ul {
    align-items: unset;
    justify-content: unset;
    margin: 0;
    margin-right: auto;
  }
}

.social-icons li {
  margin: 0 4%;
}

@media (min-width: 768px) {
  .social-icons li {
    margin: 0 20% 0 20%;
  }
}

@media (min-width: 1024px) {
  .social-icons li {
    margin: 0 8% 0 8%;
  }
}

.social-icons li li:first-child {
  margin-left: 0;
}

@media (min-width: 1024px) {
  .social-icons li li:first-child {
    margin-left: 0;
  }
}

.social-icons li li:last-child {
  margin-right: 0;
}

@media (min-width: 1024px) {
  .social-icons li li:last-child {
    margin-right: 0;
  }
}

/*
.social-icons li:first-child {
    margin-left: 0;

    @media (min-width: 1024px) {
	margin-left: 0;
    }
}

.social-icons li:last-child {
    	margin-right: 0;

    @media (min-width: 1024px) {
	margin-right: 0;
    }
}
*/
.social-icons a {
  display: block;
  margin: 0;
}

.social-icons svg {
  opacity: 1;
  fill: var(--Theme-Color-Grey);
  width: 50px;
  height: 50px;
  padding: 10px;
}

.social-icons svg:hover, .social-icons svg :focus {
  fill: var(--Theme-Color-Grey-Dk);
  transform: scale(1.1);
  transition: 0.3s ease-in-out;
}

/*
.social-icons svg:hover,
.social-icons svg:focus {
    fill: var(--Theme-Color-Grey-Dk);
    transform: scale(1.1);
    transition: 0.3s ease-in-out;
}
*/
.copyright {
  letter-spacing: 1px;
  text-transform: uppercase;
  margin-bottom: 20px;
}

@media (min-width: 768px) {
  .copyright {
    margin-top: 5px;
    margin-bottom: 30px;
  }
}

@media (min-width: 1024px) {
  .copyright {
    margin-top: 30px;
    margin-bottom: 30px;
    margin-left: auto;
  }
}

/* ========================================================================== */
/* Modules - Where styles are written for each module as stand-alone,         */
/*           reusable components                                              */
/* ========================================================================== */
/* ========================================================================== */
/* _Module Imports                                                            */
/* ========================================================================== */
/*-------------------------------*/
/*   portfolio.scss              */
/*-------------------------------*/
.portfolio {
  overflow: hidden;
  background-color: var(--Theme-Color-White);
  border: 2px solid var(--Theme-Color-Black);
  position: relative;
  transition: transform 0.8s ease-out;
}

.portfolio figcaption {
  display: none;
}

.portfolio img {
  display: block;
  width: 100%;
}

.portfolio img:hover {
  cursor: pointer;
  filter: blur(2px) brightness(45%);
}

.portfolio img:hover ~ figcaption {
  position: absolute;
  display: block;
  width: 100%;
  top: 45%;
  text-align: center;
  color: var(--Theme-Color-White);
}

@media (min-width: 768px) {
  .portfolio {
    flex-direction: row;
    flex: 1 1 45%;
    border: 1px solid var(--Theme-Color-Black);
  }
}

.portfolio--doublewidth {
  grid-column-start: 1;
  grid-column-end: 3;
}

/* ========================================================================== */
